.App {
  font-family: sans-serif;
  text-align: center;
  height: 100vh;
  width: 100vw;
}
.overlay {
  height: 100vh;
  width: 100vw;
  background-color: black;
}
.modal-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  background-color: white;
  padding: 16px;
  width: 350px;
  height: 500px;
  overflow-y: auto;
}
.title-container {
  display: flex;
  justify-content: space-between;
}
.title {
  color: black;
  text-align: left;
}
input {
  width: 100%;
  height: 30px;
  border-radius: 5px;
  border-color: grey;
  text-indent: 300px;
}
.item-header {
  text-align: left;
  margin: 0;
}
.item-container {
  display: flex;
  gap: 5px;
  padding: 16px 0px;
}
.card {
  border-radius: 15px;
  background-color: grey;
  width: 100px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card.active {
  background-color: yellow;
}
.field-container {
  position: relative;
  padding: 16px 0px;
}
.field-upper-section {
  text-align: right;
  margin: 0;
  padding: 5px 0px;
  display: flex;
  align-items: center;
  justify-content: end;
}
.field-lower-section {
  display: flex;
  align-items: center;
  justify-content: end;
}
.icon-container {
  display: flex;
  gap: 5px;
}
img {
  height: 20px;
}
.icon-text {
  margin: 0;
}
.icons-wrapper {
  display: flex;
  gap: 5px;
}
.lower-text {
  margin: 0;
  padding: 6px 0px;
}
.hide-text-wrapper {
  display: flex;
  align-self: center;
  justify-content: center;
  gap: 5px;
}
.hide-text {
  margin: 0;
  font-family: "Times New Roman", Times, serif;
  cursor: pointer;
}
.arrow {
  margin: 0;
}
.list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 0px;
}
.left-text {
  margin: 0;
  font-family: "Times New Roman", Times, serif;
}
.right-text {
  margin: 0;
}
ul {
  padding: 0px 16px;
}
li {
  text-align: left;
}
.price-tag-continer {
  position: absolute;
  left: 0;
  display: flex;
  gap: 5px;
  bottom: 10px;
}
.price-tag {
  border-radius: 10px;
  background-color: grey;
  width: 100px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
.enable-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
